import { CBadge, CButton, CCard, CCardBody, CDataTable, CInput, CTooltip, CCollapse} from '@coreui/react'
import {React, useState} from 'react'
import { useHistory } from 'react-router-dom'

const Beranda = () =>{
    const history = useHistory()
    const [details, setDetails] = useState([])
    const toggleDetails = (index) => {
      const position = details.indexOf(index)
      let newDetails = details.slice()
      if (position !== -1) {
        newDetails.splice(position, 1)
      } else {
        newDetails = [...details, index]
      }
      setDetails(newDetails)
    }    
    return(
        <>
        <CCard>
            <CCardBody>
                <div className="d-flex flex-row justify-content-center mb-4">
                    <h5>Beranda Permohonan</h5>
                </div>
                <div className="d-flex flex-row justify-content-end mb-3">
                    <div className="mr-2">
                        <CInput size="sm" style={{backgroundColor:'#f2f2f2'}} placeholder="Search" />                        
                    </div>
                    <div className="mr-2">
                        <CButton style={{borderRadius:'7px'}} className="btn btn-light btn-sm"><i class="fas fa-search"></i></CButton>
                    </div>
                    <div>
                        <CButton style={{borderRadius:'7px', backgroundColor:'#4dbd74', color:'white'}} onClick={()=>history.push('/ppbj/new')} className="btn btn-sm"><i class="fas fa-plus"></i></CButton>
                    </div>                    
                </div>                
                <CDataTable
                addTableClasses="tablebiru"
                fields={[
                    {key:'no', label:'Nomor'},
                    {key:'noaju', label:'Nomor Pengajuan'},
                    {key:'noppbj', label:'Nomor PPBJ'},
                    {key:'tglppbj', label:'Tanggal PPBJ'},
                    {key:'refppbj', label:'Referensi PPBJ'},
                    {key:'lawantrx', label:'Lawan Transaksi'},
                    {key:'objtrx', label:'Objek Transaksi'},
                    {key:'status', label:'Status'},
                    {key:'aksi', label:'Aksi'},                    
                ]}
                items={[
                    {
                        no:'1',
                        noaju:'901017D9867A3',
                        noppbj:'20051000001',
                        tglppbj:'14-05-2021',
                        refppbj:'829374837263',
                        lawantrx:'PT CANDI',
                        objtrx:'BKP Berwujud',
                        status:'Pengajuan'
                    },
                    {
                        no:'2',
                        noaju:'901017D0247C1',
                        noppbj:'20071000002',
                        tglppbj:'21-07-2021',
                        refppbj:'829374927263',
                        lawantrx:'PT Lux Corp',                        
                        objtrx:'JKP',
                        status:'Selesai'
                    },
                    {
                        no:'3',
                        noaju:'901017D3621D0',
                        noppbj:'20081000003',
                        tglppbj:'24-08-2021',
                        refppbj:'8293748124123',
                        lawantrx:'CV Jaya Abadi',                        
                        objtrx:'BKP Berwujud',
                        status:'Selesai'
                    }, 
                    {
                        no:'4',
                        noaju:'901017D9627E1',
                        noppbj:'20091000004',
                        tglppbj:'24-09-2021',
                        refppbj:'8293748112344',
                        lawantrx:'CV Sukar Maju',                        
                        objtrx:'BKP Berwujud',
                        status:'Pembatalan'
                    }                                                           
                ]}
                scopedSlots={{
                    'aksi':
                    (item, index) =>{
                        let obj = {}
                        if(item.status == "Pengajuan"){obj = {view:true,play:true, ban:true, cloud:false}}
                        else if(item.status == "Selesai"){obj = {view:true,play:false, ban:false, cloud:true}}
                        else if(item.status == "Pembatalan"){obj = {view:true,play:false, ban:false, cloud:true}}
                        return(
                            <td>
                                <div className="d-flex flex-row">
                                    <div className={obj.view ? "mr-1" : ""}>
                                    <CButton style={obj.view ? {backgroundColor:'#02275d', color:'white', borderRadius:'7px'} : {display:'none'}} className="btn btn-sm"><i class="fas fa-eye"></i></CButton>                                        
                                    </div>
                                    <div className={obj.play ? "mr-1" : ""}>
                                    <CButton style={obj.play ? {backgroundColor:'#517fa4', color:'white', borderRadius:'7px'} : {display:'none'}} className="btn btn-sm"><i class="far fa-play-circle"></i></CButton>                                        
                                    </div>  
                                    <div className={obj.ban ? "mr-1" : ""}>
                                    <CButton style={obj.ban ? {backgroundColor:'#f76c6b', color:'white', borderRadius:'7px'} : {display:'none'}} className="btn btn-sm"><i class="fas fa-ban"></i></CButton>                                        
                                    </div>   
                                    <div className={obj.cloud ? "mr-1" : ""}>
                                    <CButton style={obj.cloud ? {backgroundColor:'#c8ced3', color:'white', borderRadius:'7px'} : {display:'none'}} className="btn btn-sm"><i class="fas fa-cloud-download-alt"></i></CButton>                                        
                                    </div>                                                                                                          
                                </div>
                            </td>
                        )
                    },
                    'status':
                    (item, index)=>{
                        let color 
                        if(item.status == "Pengajuan"){
                            color = "#64c2de"
                        } else if (item.status == "Selesai"){
                            color  = "#4dbd74"
                        } else if (item.status == "Pembatalan"){
                            color = "#2f353a"
                        }
                        return(
                            <td>
                                <CTooltip content="Click me for tracking">
                                    <CBadge onClick={()=>toggleDetails(index)} role="button" style={{backgroundColor:`${color}`, color:'white'}}>{item.status}</CBadge>                                    
                                </CTooltip>
                            </td>
                        )
                    },
                    'details':
                    (item, index)=>{
                        let arr
                        if(item.status == "Pengajuan"){arr = [{status:'Draft', timestamp:'2021-07-22 21:07:34'},{status:'Pengiriman DJP', timestamp:'2021-07-23 07:03:05'},{status:'Pengiriman CEISA', timestamp:'2021-07-23 07:04:10'}]}
                        else if(item.status == "Selesai"){arr = [{status:'Draft', timestamp:'2021-07-22 21:07:34'},{status:'Pengiriman DJP', timestamp:'2021-07-23 07:03:05'},{status:'Pengiriman CEISA', timestamp:'2021-07-23 07:04:10'},{status:'Endorsement DJP', timestamp:'2021-07-26 10:04:10'}, {status:'Faktur & SSP Terbit', timestamp:'2021-07-26 18:15:10'}]}
                        else if(item.status == "Pembatalan"){arr = [{status:'Draft', timestamp:'2021-07-22 21:07:34'},{status:'Pengiriman DJP', timestamp:'2021-07-23 07:03:05'},{status:'Pengiriman CEISA', timestamp:'2021-07-23 07:04:10'},{status:'Pengiriman Pembatalan DJP', timestamp:'2021-07-26 10:04:10'}, {status:'Pengiriman Pembatalan CEISA', timestamp:'2021-07-26 10:10:10'}]}
                      return (
                      <CCollapse show={details.includes(index)}>
                          <CCard>
                              <CCardBody>
                                  {
                                      arr.map((x)=>{
                                          return(
                                              <div className="d-flex flex-row justify-content-end mb-1">
                                                  <div style={{minWidth:'200px'}} className="mr-3 d-flex align-items-start"><CBadge color="light">{x.status}</CBadge></div>
                                                  <div><CBadge color='info'>{x.timestamp}</CBadge></div>
                                              </div>
                                          )
                                      })
                                  }
                              </CCardBody>
                          </CCard>
                      </CCollapse>
                    )
                  }                    
                }}
                />
            </CCardBody>
        </CCard>
        </>
    )
}

export default Beranda